@import 'primeicons/primeicons.css';

.dlct-wrapper {
  .ml-4 {
    margin-left: 4px;
  }
  background-color: #fff;
  margin-right: 20px;
  margin-top: 20px;

  .p-toolbar {
    border: none !important;
  }
  .p-multiselect .p-multiselect-label {
    padding: 0.65rem 0.75rem;
    font-weight: 600;
  }
  .p-multiselect {
    height: 2.8em;
  }

  .p-button.p-button-sm {
    padding: 0.4rem 0.5rem;
    height: 2.7em;
  }

  .p-component {
    font-size: 0.9rem;
  }

  .p-menubar {
    border-radius: 0;
    font-weight: 500;
  }

  .p-menuitem-icon {
    font-weight: 500;
  }

  .router-link-active {
    background-color: #d4d4d8;
    border-radius: 4px;
  }
  .message{
    border: solid #bfdbfe;
    border-width: 1px;
    color: #2563eb;
    background: rgba(239, 246, 255, 0.95);
    padding: 0.5rem 0.75rem;
    width: 100%;
  }
  .dlct-form {
    padding: 20px;

    &-item {
      margin-bottom: 10px;
      display: flex;
      gap: 20px;
      justify-content: space-between;
      max-width: 700px;

      label {
        font-weight: bold;
        font-size: 14px;
      }
    }
    .p-multiselect{
      width: 320px;
    }
    .highlight{
      color: #d96554;
    }
  }

  .p-datatable-tbody > tr {
    &.highlight-error-fatal {
      padding: 10px;
      background-color: rgb(253, 232, 232);
    }

    &.highlight-error-warning {
      padding: 10px;
      background-color: rgb(253, 252, 232);
    }

    &.highlight-error-deprecated {
      padding: 10px;
      background-color: rgb(245, 245, 244);
    }

    &.highlight-error-info {
      padding: 10px;
      background-color: aliceblue;
    }

    &.highlight-error-parse {
      padding: 10px;
      background-color: rgb(255, 240, 255);
    }
  }

  .menu-page {
    padding: 20px;

    .info {
      font-size: 16px;
    }
  }

  .p-paginator-element {
    min-width: 2rem !important;
    height: 2rem !important;
  }

  .p-dropdown {
    height: 2rem !important;

    span {
      align-self: center !important;
    }
  }

  .table-header {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    align-items: center;

    .log-stats {
      display: flex;
      gap: 10px;
      margin-right: auto;

      .log-count {
        display: flex;
        align-items: center;
        gap: 8px;
        padding: 4px 8px;
        background-color: #f0f9ff;
        border-radius: 4px;
        border: 1px solid #bae6fd;

        .log-count-text {
          font-size: 0.85rem;
          color: #0369a1;
          font-weight: 500;
        }
      }

      .file-size {
        display: flex;
        align-items: center;
        gap: 6px;
        padding: 4px 8px;
        background-color: #f0fdf4;
        border-radius: 4px;
        border: 1px solid #86efac;

        i {
          color: #16a34a;
          font-size: 0.9rem;
        }

        .file-size-text {
          font-size: 0.85rem;
          color: #16a34a;
          font-weight: 500;
        }
      }
    }

    .error-type-filter {
      max-width: 200px !important;
      min-width: 150px !important;

      .p-multiselect-label {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .p-multiselect-token {
        max-width: 80px;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .auto-refresh-controls {
      display: flex;
      align-items: center;
      gap: 8px;
      margin-right: 10px;
      padding: 4px 8px;
      background-color: #f9fafb;
      border-radius: 4px;
      border: 1px solid #e5e7eb;

      .auto-refresh-label {
        font-size: 0.8rem;
        color: #4b5563;
        white-space: nowrap;
      }

      .refresh-interval-dropdown {
        width: 120px;
        height: 2rem;
        margin-left: 5px;
      }
    }
  }

  .p-column-title {
    color: #4a4e56;
  }

  .log-table {
    .p-datatable-header {
      padding: 10px;
      border: 1px solid #e5e7eb;
    }
  }
  .query-table{
    padding: 10px;
  }

  .p-datatable > .p-datatable-wrapper,
  .dlct-form,
  .menu-page {
    border: 1px solid #e5e7eb;
  }

  .p-datatable .p-datatable-tbody > tr > td {
    padding: 0.7rem 0.8rem;
    border: 1px solid #f4f4f5;
  }
}

.notice-success {
  margin-left: 0px;
}
.dlct-error-msg span{
  background: rgba(254, 242, 242, 0.95);
  border-width: 1px;
  padding: 5px;
  color: #dc2626;
}
.p-listbox{
  min-width: 350px;
}
.query-log-list {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.query-trace li{
  border: 1px solid #e5e7eb;
  padding: 5px;
  margin-bottom: 0;
}
.p-tabview-nav-link ,.p-accordion-header-action{
  border-radius: 0px;
}
.p-tabview-panels {
  padding: 0px;
}
li.p-tabview-header{
  margin-bottom: 0px;
}
.index-number{
  color: #a8aaad;
  font-weight: lighter;
}
.query-log-list{
 color: #4a4e56!important;
}

/* Stack Trace Dialog Styles */
.error-details {
  padding: 1rem;

  .error-message, .error-location {
    margin-bottom: 1.5rem;

    h3 {
      font-size: 1.1rem;
      font-weight: 600;
      margin-bottom: 0.5rem;
      color: #374151;
    }
  }

  .error-message {
    padding: 1rem;
    background-color: #fef2f2;
    border-radius: 4px;
    border-left: 4px solid #ef4444;
  }

  .error-location {
    padding: 1rem;
    background-color: #f3f4f6;
    border-radius: 4px;
    font-family: monospace;
  }
}

.mt-2 {
  margin-top: 0.5rem;
}

.log-entry-header {
  display: flex;
  align-items: flex-start;

  .log-count-badge {
    margin-left: 8px;
    flex-shrink: 0;
  }
}

.time-with-tooltip {
  cursor: pointer;
  border-bottom: 1px dotted #ccc;

  &:hover {
    color: #2563eb;
  }
}
